.App{
  margin:0px;
}

.blue-shadow{
  background-color:rgb(255, 0, 221);
  position:absolute;
  border-radius: 50%;
  width: 25vw;
  height:25vh;
  left: -15vw;
  bottom: 00px;
  z-index:-1;
}

.green-shadow{
  background-color:aqua;
  position:absolute;
  border-radius: 20%;
  width: 25vw;
  height:25vh;
  right: -25vw;
  top: -10vh;
  transform: rotate(45deg);
  z-index:-1;
}